body {
  color: #fff;
  background: #1e2230 url("../../images/download/logo-footer.png") 0 bottom no-repeat;
  background-size: 100%;
}
.module-download {
  padding: 32px 0;
  background: #1e2230 url("../../images/download/bg.jpg") 0 0 no-repeat;
  background-size: 100%;
}
.module-download .download-header {
  margin-bottom: 16px;
  text-align: center;
}
.module-download .download-header .header-logo {
  margin-bottom: 10px;
}
.module-download .download-header .header-title {
  font-weight: bold;
  font-size: 24px;
  line-height: 1;
}
.module-download .download-header .header-www {
  opacity: 0.85;
}
.module-download .download-body {
  margin-bottom: 20px;
  text-align: center;
}
.module-download .download-body .body-text {
  padding: 0 1rem;
  margin-bottom: 16px;
}
.module-download .download-body .body-text .body-p1 {
  font-style: italic;
  font-size: 18px;
}
.module-download .download-body .body-text .body-p2 {
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 10px 0;
}
.module-download .download-body .body-text .body-p2 .p2-left,
.module-download .download-body .body-text .body-p2 .p2-right {
  height: 30px;
  border-right: 5px solid #fff;
}
.module-download .download-body .body-text .body-p2 .p2-left {
  margin-right: 4px;
}
.module-download .download-body .body-text .body-p2 .p2-right {
  margin-left: 4px;
}
.module-download .download-body .body-text .body-p2 .p2-text {
  line-height: 1;
  vertical-align: middle;
  font-size: 32px;
  letter-spacing: 3px;
  font-weight: bold;
}
.module-download .download-body .body-text .body-p3 {
  opacity: 0.85;
}
.module-download .download-body .body-text .body-p3 .p3-text {
  letter-spacing: 4px;
}
.module-download .download-footer .download {
  display: flex;
  justify-content: center;
  padding: 0 10px;
}
.module-download .download-footer .download-btn:first-child {
  margin-right: 16px;
}
.module-download .download-footer .download-btn .download-link {
  display: block;
}
.module-download .download-footer .download-btn .download-link img {
  height: 52px;
}
.module-download .download-footer .footer-logo {
  margin-top: 20px;
}
.module-download .mask {
  display: none;
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.75);
  z-index: 9999;
}
.module-download .mask .arrow {
  width: 88px;
  height: 125px;
  position: absolute;
  top: 8px;
  right: 30px;
}
.module-download .mask .arrow img {
  width: 100%;
  height: 100%;
}
.module-download .mask .arrow-text {
  position: absolute;
  top: 150px;
  left: 50%;
  width: 100%;
  transform: translate(-50%, 0);
  font-size: 16px;
  text-align: center;
  color: #fff;
}
.module-download .mask .img {
  width: 50px;
  height: 50px;
  float: right;
}
.module-download .mask .info {
  width: 100%;
  text-align: center;
  padding-top: 2rem;
  clear: both;
}
.module-download .mask .info .info-text {
  font-size: 18px;
  font-weight: 700;
}
